.framePrincipal {
  background-image: url(../img/imgBgPrincipal.png);
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.framePrincipal ul, .framePrincipal li {
  margin: 0;
  padding: 0;
}
.framePrincipal ul {
  height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  overflow: hidden;
}
.framePrincipal li {
  width: 50%;
  height: 50%;
  transition: background 0.3s;
}
.framePrincipal li:hover {
  background: #000;
  cursor: pointer;
  transition: background 0.3s;
}
.framePrincipal li h3 {
  color: white;
  transform: rotate(-45deg);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.framePrincipal .framePrincipal_menu {
  width: 25rem;
  height: 25rem;
  margin-top: 8rem;
  backdrop-filter: blur(40px);
  overflow: hidden;
  transform: rotate(45deg);
  position: relative;
  /* L√≠nea horizontal */
}
.framePrincipal .framePrincipal_menu h3 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .framePrincipal .framePrincipal_menu {
    width: 38rem;
    height: 38rem;
  }
  .framePrincipal .framePrincipal_menu h3 {
    font-size: 3rem;
  }
}
.framePrincipal .framePrincipal_menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 100%;
  background-color: white;
  transform: translateX(-50%);
  z-index: 101;
}
.framePrincipal .framePrincipal_menu::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: white;
  transform: translateY(-50%);
  z-index: 100;
}
.framePrincipal .framePrincipal_titular p {
  margin-bottom: 2rem;
  font-weight: 400;
  margin: 1rem;
}
.framePrincipal .framePrincipal_titular h1 {
  font-weight: 700;
}

